import { defineComponent, onMounted, reactive, toRefs, watch } from 'vue';
import { getUserList, getRoleList } from '../request/api';
import { InitData } from '../type/user';
export default defineComponent({
  setup() {
    // 定义数据
    const data = reactive(new InitData());
    // 定义函数
    const getUser = async () => {
      await getUserList().then(res => {
        data.list = res.data;
      });
    };
    const getRole = async () => {
      await getRoleList().then(res => {
        data.roleList = res.data;
      });
    };
    const onSubmit = () => {
      let arr = [];
      if (data.selectData.nickName || data.selectData.role) {
        if (data.selectData.nickName) {
          arr = data.list.filter(val => val.nickName.indexOf(data.selectData.nickName) !== -1);
        }
        if (data.selectData.role) {
          arr = (data.selectData.nickName ? arr : data.list).filter(val => val.role.find(item => item.role === data.selectData.role));
        }
      } else {
        arr = data.list;
      }
      data.list = arr;
    };
    const changeUser = row => {
      data.isShow = true;
      data.active = {
        id: row.id,
        nickName: row.nickName,
        userName: row.userName,
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        role: row.role.map(val => val.role || val.roleId)
      };
    };
    const updateUser = () => {
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      const obj = data.list.find(val => val.id === data.active.id);
      obj.nickName = data.active.nickName;
      obj.role = data.roleList.filter(val => data.active.role.indexOf(val.roleId) !== -1);
      data.list.forEach((item, i) => {
        if (item.id === obj.id) {
          data.list[i] = obj;
        }
      });
      data.isShow = false;
    };
    // 使用函数
    onMounted(async () => {
      getUser();
      getRole();
    });
    watch([() => data.selectData.nickName, () => data.selectData.role], () => {
      if (data.selectData.nickName === '' && data.selectData.role === 0) {
        getUser();
        getRole();
      }
    });
    // 导出
    return {
      ...toRefs(data),
      onSubmit,
      changeUser,
      updateUser
    };
  }
});