interface RoleInt{
  role:number
  roleName:string
}
interface ListInt{
  id:number
  nickName:string
  userName:string
  role:RoleInt[]
}
interface SelectDataInt{
  role:number
  nickName:string
}
interface RoleListInt{
  authority:number[]
  roleId:number
  roleName:string
}
interface ActiveInt{
  id:number
  nickName:string
  userName:string
  role:number[]
}

class InitData {
  list:ListInt[]=[]// 接受用户信息
  roleList:RoleListInt[]=[]// 接收角色信息
  isShow=false
  selectData:SelectDataInt={
    role: 0,
    nickName: ''
  }

  active:ActiveInt={
    id: 0,
    nickName: '',
    userName: '',
    role: []
  }
}

export { InitData, ListInt }
